.wrapper {
  position: relative;
  width: 100%;
  height: 60%;
  margin: 0 auto;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.circle, .square {
  width: calc(100vw / 4);
  height: calc(100vw / 4);
}

.circle {
  border-radius: 50%;
  background-image: url('/images/me_irl.png');
  background-position: center;
  background-size: cover;
  border: 30px double var(--bg-colour);
}

.square {
  font-size: 1.8rem;
  font-weight: bold;
  background: -webkit-repeating-linear-gradient(135deg, turquoise, turquoise 10px, darkturquoise 10px, darkturquoise 20px);
  background: -o-repeating-linear-gradient(135deg, turquoise, turquoise 10px, darkturquoise 10px, darkturquoise 20px);
  background: repeating-linear-gradient(-45deg, turquoise, turquoise 10px, darkturquoise 10px, darkturquoise 20px);
  border: 10px dashed var(--bg-colour);
  color: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}


